<template>
  <div class="profile-page">
    <h2>玩家信息</h2>
    <div class="user-info">
      <p>玩家名称: {{ currentPlayer }}</p>
      <input 
        type="text" 
        v-model="currentPlayer"
        @change="changePlayer"
        placeholder="输入玩家名称"
      >
    </div>
    <div class="game-history" v-if="gameHistory.length">
      <h3>游戏历史记录</h3>
      <ul>
        <li v-for="(record, index) in gameHistory" :key="record.id">
          第{{ index + 1 }}次游戏 | {{ record.playDate }} | 分数: {{ record.score }} | 结果: {{ record.result === 'win' ? '胜利' : '失败' }}
        </li>
      </ul>
    </div>
    <div v-else class="no-records">
      暂无游戏记录
    </div>
  </div>
</template>

<script>
import { store } from '../store/playerStore'

export default {
  name: 'ProfilePage',
  data() {
    return {
      currentPlayer: store.playerName || 'zyh',
      gameHistory: []
    }
  },
  methods: {
    async fetchGameHistory() {
      try {
        const response = await fetch(`/api/player/${this.currentPlayer}/history`);
        if (!response.ok) {
          throw new Error('获取游戏历史失败');
        }
        this.gameHistory = await response.json();
        // 按日期排序
        this.gameHistory.sort((a, b) => new Date(b.playDate) - new Date(a.playDate));
      } catch (error) {
        console.error('获取数据失败:', error);
      }
    },
    async changePlayer() {
      if (this.currentPlayer.trim()) {
        store.setPlayerName(this.currentPlayer);
        await this.fetchGameHistory();
      }
    }
  },
  async created() {
    await this.fetchGameHistory();
  }
}
</script>

<style scoped>
.profile-page {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.user-info {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.user-info input {
  margin-top: 10px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 200px;
}

.game-history {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.no-records {
  margin-top: 20px;
  padding: 20px;
  text-align: center;
  color: #666;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

li:last-child {
  border-bottom: none;
}
</style>
