<template>
  <div class="game-page">
    <div class="player-name-input">
      <input 
        type="text" 
        v-model="playerName" 
        @change="updatePlayerName"
        placeholder="输入玩家名称"
      >
    </div>
    <div id="game1" class="game-div"></div>
  </div>
</template>

<script>
import { store } from '../store/playerStore'  // 导入store

export default {
  name: 'GamePage',
  data() {
    return {
       playerName: store.playerName || '',
    }
  },
  methods: {
    updatePlayerName() {
      if (this.playerName.trim()) {
        // 更新全局状态和window变量
        store.setPlayerName(this.playerName);
        window.currentPlayerName = this.playerName;
      }
    }
  },


  mounted() {
    // 设置全局变量供 Phasergame.js 使用
    window.currentPlayerName = this.playerName;
    
    const phaserScript = document.createElement('script')
    phaserScript.src = 'https://cdn.jsdelivr.net/npm/phaser@2.4.2/build/phaser.min.js'

    phaserScript.onload = () => {
      const gameScript = document.createElement('script')
      gameScript.src = 'https://github.com/zyhzip/flask/blob/master/Phasergame.js'
      document.body.appendChild(gameScript)
    }
    document.body.appendChild(phaserScript)

  },


  beforeUnmount() {
    if (window.game) {
      window.game.destroy && window.game.destroy();
    }
    const scripts = document.querySelectorAll('script')
    scripts.forEach(script => {
      if (script.src.includes('phaser') || script.src.includes('Phasergame')) {
        script.remove()
      }
    })
    // 清理全局变量
    delete window.currentPlayerName;
  }
}
</script>



<style scoped>
.game-page {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5eeee;
}

.player-name-input {
  margin-bottom: 20px;
}

.player-name-input input {
  padding: 8px 12px;
  font-size: 16px;
  border: 2px solid #42b983;
  border-radius: 4px;
  outline: none;
}

.player-name-input input:focus {
  border-color: #3aa876;
}

.game-div {
  width: 800px;
  height: 600px;
}
</style>